export default {
	baseUrl: 'https://wmjsapi.perfectrace.cn/mycontroller/app/',
	// baseUrl: 'http://192.168.1.11:8081/mycontroller/app/',
	// baseUrl: 'https://wmjsapi.perfectrace.cn/mycontroller/app/',
	imageUrl:'https://wmjsapi.perfectrace.cn/images/',
	// baseUrl: 'https://chatapp.shangxin6688.com',
	//   yyyy-mm-dd hh-mm-ss
	timestampToTime: timestamp => {
		if (!timestamp) {
			timestamp = new Date().getTime();
		}
		const dateObj = new Date(timestamp) // ps, , +
		const year = dateObj.getFullYear() // 
		const month = dateObj.getMonth() + 1 // 10
		const date = dateObj.getDate() // getDay()
		const hours = dateObj.getHours() >= 10 ? dateObj.getHours() : '0' + dateObj.getHours(); // 
		const minutes = dateObj.getMinutes() >= 10 ? dateObj.getMinutes() : '0' + dateObj.getMinutes(); //  
		const seconds = dateObj.getSeconds() >= 10 ? dateObj.getSeconds() : '0' + dateObj.getSeconds(); // 
		const haomiao = dateObj.getMilliseconds() >= 10 ? dateObj.getMilliseconds() : '0' + dateObj.getMilliseconds(); // 
		return year + '-' + month + '-' + date + ' ' + hours + ':' + minutes + ':' + seconds
	},
	//   yyyy-mm-dd 
	timestampToDay: daystamp => {
		if (!daystamp) {
			daystamp = new Date().getTime();
		}
		const dateObj = new Date(daystamp) // ps, , +
		const year = dateObj.getFullYear() // 
		const month = dateObj.getMonth() + 1 // 10
		const date = dateObj.getDate() // getDay()
		return year + '-' + month + '-' + date
	},
	//1  2 
	interval: (faultDate, completeTime) => {
		var stime = Date.parse(new Date(faultDate));
		var etime = Date.parse(new Date(completeTime));
		// console.log(stime)
		// console.log(etime)

		var usedTime = etime - stime; //
		var days = Math.floor(usedTime / (24 * 3600 * 1000));
		//
		var leave1 = usedTime % (24 * 3600 * 1000); //
		var hours = Math.floor(leave1 / (3600 * 1000));
		//
		var leave2 = leave1 % (3600 * 1000); //
		var minutes = Math.floor(leave2 / (60 * 1000));
		return [days, hours, minutes];
	},
	//
	// dayCount  
	getDateStr : dayCount => {
		let dd = new Date();
		console.log(dd)
		dd.setDate(dd.getDate() + dayCount)
		let time = dd.getTime()
		return time + ''
	},
	// 
	//date   yyyy-mm-dd
	//day  
	getNextDate: (date, day) =>{  
	  let dd = new Date(date);
	  dd.setDate(dd.getDate() + day);
	  let y = dd.getFullYear();
	  let m = dd.getMonth() + 1 < 10 ? "0" + (dd.getMonth() + 1) : dd.getMonth() + 1;
	  let d = dd.getDate() < 10 ? "0" + dd.getDate() : dd.getDate();
	  return y + "-" + m + "-" + d;
	},
	//url  
	//data json
	//type  
	requests: function(url, datas, type) {
		var that = this;
		return new Promise((resolve, reject) => {
			var data = datas || {};
			if (url == undefined) {
				console.log('URL');
				return false;
			}
			if (data.token == undefined) {
				data.token = uni.getStorageSync('token');
				// data.token = '575234b3-e88c-4136-9f7f-571627719ef0';
			}
			if (type == undefined) {
				type = 'POST';
			}
			uni.request({
				url: that.baseUrl + url,
				header: {
					'Content-Type': 'application/json;charset=UTF-8',
					'Content-Type': 'application/x-www-form-urlencoded'
				},
				data: data,
				// sslVerify: false, 
				method: type,
				success: (res) => {
					// console.log(res.data.code)
					resolve(res)
					if(res.data.code == 401){
						uni.showModal({
							content: '?',
							success: res => {
								if (res.confirm) {
									uni.reLaunch({
										url: '/pages/quickLogin/quickLogin'
									});
								}
							}
						});
					}
					if(res.data.code == 402){
						uni.showModal({
							content: '',
							success: res => {
								if (res.confirm) {
									console.log(555525)
									uni.navigateTo({
										url:'/pages/quickLogin/quickLogin'
									})
								}
							}
						});
					}
				},
				fail: (err) => {
					reject(err)
				}
			})
		})
	},
	///
	//count  number   1
	//isup  //  1:  2:  1
	//stypearr  arr   1   2 
	//sizeTypearr arr   2 1  2 
	async upimg(count, isup, stypearr, sizeTypearr){
		var that = this;
		let urlList = [];
		let imgList = await that.reqImg(count, stypearr, sizeTypearr);
		if(!isup || isup == 1 ){
			for(var i = 0,len = imgList.length; i < len; i++){
				urlList.push(await that.addimg(imgList[i]));
			}
			return urlList;
		}else if(isup == 2){
			return imgList;
		}
	},
	//
	reqImg(count, stypearr, sizeTypearr){
		var that = this;
		return new Promise((resolve, reject) =>{
			let sourceType = ['album', 'camera'];
			let sizeType = ['compressed']
			// , 
			uni.chooseImage({
				sourceType: stypearr ? stypearr:sourceType,
				sizeType: sizeTypearr ? sizeTypearr : sizeType,
				count: count?count:1,
				success: (res) => {
					 resolve(res.tempFilePaths)
				},
				fail(e) {
				 	reject(e)
				 }
			})
		})	
	},
	//
	addimg(imgfil){
		return new Promise((resolve, reject) =>{
			var that = this;
			// console.log(imgfil)
			// console.log(that.baseUrl + 'upload')
			uni.uploadFile({//,,:
				url: that.baseUrl + 'upload', 
				file: '',//files,filePathname
				filePath: imgfil,
				name: 'mf',
				// enctype: "multipart/form-data",
				formData: {//post
					'token': uni.getStorageSync('token'),//id
				},
				success: (uploadFileRes) => {
					console.log(JSON.parse(uploadFileRes.data))
					resolve(JSON.parse(uploadFileRes.data).result)
					  // resolve(JSON.parse(uploadFileRes.data).data.url)
				},
				fail: (e) => {
					    this.$api.msg('')
						reject(e)
				}
			});
		})
	},
	//K M G T
	getfilesize(size) {
	        if (!size)
	            return "";
	        var num = 1024.00; //byte
	        if (size < num)
	            return size + "B";
	        if (size < Math.pow(num, 2))
	            return (size / num).toFixed(2) + "K"; //kb
	        if (size < Math.pow(num, 3))
	            return (size / Math.pow(num, 2)).toFixed(2) + "M"; //M
	        if (size < Math.pow(num, 4))
	            return (size / Math.pow(num, 3)).toFixed(2) + "G"; //G
	        return (size / Math.pow(num, 4)).toFixed(2) + "T"; //T
	},
	peoNum:(count)=>{
		let peoCount = Number(count)
		if(peoCount){
			if(peoCount/10000>1){
				return (peoCount/10000).toFixed(1)+''
			}else{
				return peoCount
			}
		}else{
			return 0
		}
	},
	// 00:00
	secondFormact:(second)=>{
		var sec = second % 60;
		var min = Math.floor(second / 60);
		if (min.toString().length < 2) {
			min = '0' + min;
		}
		if (sec.toString().length < 2) {
			sec = '0' + sec;
		}
		return min + ':' + sec
	}
	
}
